.container {
  height: 100%;
  margin-left: auto;
  margin-right: auto;

  @include media-max(mobile) {
    min-width: 320px;
    max-width: 573px;
    padding-left: 16px;
    padding-right: 16px;
  }

  @include media-min(tablet) {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @include media-min(desktop) {
    padding-left: 256px;
    padding-right: 256px;
    max-width: 1440px;
  }
}
